
audio,canvas,progress,video{vertical-align:baseline
}
audio:not([controls]){display:none;height:0
}
b,strong{font-weight:bold
}
img{max-width:100%;border:0
}
svg:not(:root){overflow:hidden
}
figure{margin:1em 40px
}
hr{height:1px;border-width:0;box-sizing:content-box
}
pre{overflow:auto
}
code,kbd,pre,samp{font-family:monospace, monospace;font-size:1em
}
label,input,select,textarea{font-family:inherit;line-height:normal
}
textarea{overflow:auto;height:auto;font:inherit;color:inherit
}
form,input,optgroup,select{margin:0;font:inherit;color:inherit
}
html input[type="button"],input[type="reset"],input[type="submit"]{cursor:pointer;-webkit-appearance:button
}
a,a div,a span,a .ion-icon,a .ion-label,button,button div,button span,button .ion-icon,button .ion-label,[tappable],[tappable] div,[tappable] span,[tappable] .ion-icon,[tappable] .ion-label,input,textarea{-ms-touch-action:manipulation;touch-action:manipulation
}
a .ion-label,button .ion-label,[tappable] .ion-label{pointer-events:none
}
button{border:0;font-family:inherit;font-style:inherit;font-variant:inherit;line-height:1;text-transform:none;cursor:pointer;-webkit-appearance:button
}
a[disabled],button[disabled],html input[disabled]{cursor:default
}
button::-moz-focus-inner,input::-moz-focus-inner{padding:0;border:0
}
input[type="checkbox"],input[type="radio"]{padding:0;box-sizing:border-box
}
input[type="number"]::-webkit-inner-spin-button,input[type="number"]::-webkit-outer-spin-button{height:auto
}
input[type="search"]::-webkit-search-cancel-button,input[type="search"]::-webkit-search-decoration{-webkit-appearance:none
}
table{border-collapse:collapse;border-spacing:0
}
td,th{padding:0
}
.hide,[hidden],template{display:none !important
}
.sticky{position:-webkit-sticky;position:sticky;top:0
}
:focus,:active{outline:none
}
.focus-outline :focus{outline:thin dotted;outline-offset:-1px
}
.focus-outline button:focus,.focus-outline [ion-button]:focus{border-color:#51a7e8;outline:2px solid #51a7e8;box-shadow:0 0 8px 1px #51a7e8
}
ion-input :focus{outline:none
}
.click-block{position:absolute;top:0;right:0;bottom:0;left:0;z-index:9999;display:block;opacity:0;transform:translate3d(0, -100%, 0);transform:translate3d(0, calc(-100% + 1px), 0)
}
.click-block-active{transform:translate3d(0, 0, 0)
}
*{box-sizing:border-box;-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent;-webkit-touch-callout:none
}
html{width:100%;height:100%;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;text-size-adjust:100%
}
body{position:fixed;overflow:hidden;margin:0;padding:0;width:100%;max-width:100%;height:100%;max-height:100%;-webkit-font-smoothing:antialiased;font-smoothing:antialiased;text-rendering:optimizeLegibility;-webkit-user-drag:none;-ms-content-zooming:none;-ms-touch-action:manipulation;touch-action:manipulation;word-wrap:break-word;-webkit-text-size-adjust:none;-ms-text-size-adjust:none;text-size-adjust:none;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none
}
body .ion-app{overflow:visible
}
.ion-app,ion-nav,ion-tab,ion-tabs,.app-root{position:absolute;top:0;left:0;z-index:0;display:block;width:100%;height:100%
}
ion-nav,ion-tab,ion-tabs{overflow:hidden
}
ion-tab{transform:translateY(-200%)
}
ion-tab.show-tab{transform:translateY(0)
}
.ion-page{position:absolute;top:0;left:0;display:block;width:100%;height:100%;opacity:0
}
.ion-page.show-page{opacity:1
}
.ion-header{position:absolute;top:0;left:0;z-index:10;display:block;width:100%
}
.ion-footer{position:absolute;bottom:0;left:0;z-index:10;display:block;width:100%
}
[app-viewport],[overlay-portal],[nav-viewport],[tab-portal],.nav-decor{display:none
}
.ion-app.ios{font-family:-apple-system,"Helvetica Neue","Roboto",sans-serif;font-size:1.4rem;background-color:#fff
}
.ion-app.md{font-family:"Roboto","Helvetica Neue",sans-serif;font-size:1.4rem;background-color:#fff
}
.ion-app.wp{font-family:"Segoe UI","Noto Sans",sans-serif;font-size:1.4rem;background-color:#fff
}



